import React, { Component } from 'react'
import '../css/Style.css'
import axios from 'axios'
export class Product extends Component {

    constructor(props) {
        super(props)

        this.state = {
            Products: [],
            id: '',
            image: '',
            Category: '',
            Product_name: '',
            price: '',
            quantity: ''
        }
    }

    fetchdata = () => {
        axios.get("https://enchanting-slippers-bull.cyclic.app//Products/")
            .then((res) => {
                console.log(res.data)
                this.setState({ Products: res.data.data })
            }).catch((err) => {
                //console.log(err)
            })
    }

    componentDidMount() {
        this.fetchdata()
    }

    Cart = (id)=>{
        console.log(id);

        axios.get(`https://enchanting-slippers-bull.cyclic.app/Products/${id}`)
        .then((res)=>{
            console.log(res.data);
            const{id,image,Product_name,quantity,price} = res.data.data
            this.setState({id,image,Product_name,quantity,price})

            const cartobj ={
                _id:res.data.data._id,
                image:res.data.data.image,
                Category:res.data.data.Category,
                Product_name:res.data.data.Product_name,
                quantity:res.data.data.quantity,
                price:res.data.data.price
            }
            axios.post("https://enchanting-slippers-bull.cyclic.app/cart",cartobj)
            .then(() => {
                    window.alert("data added successfully!")
            }).catch((e)=>{
                console.log("Error: ",+ e);
            })
            // console.log(this.state);
            console.log("data added");
        })

    }




    render() {
        return (
            <div className='container-fluid'>
                <div className='container-fluid d-flex justify-content-evenly flex-wrap'>
                    {
                        this.state.Products.map((data, index) => {
                            return <div key={data._id}>
                                <div className='row  p-3'>
                                    <div className='col text-center'>
                                        <div className='card border border-dark rounded mt-2' style={{ height: 290, width: 250 }}>
                                            <img src={data.image} className="d-block w-100" style={{ height: 100 }} alt="..." />

                                            <div className='card-body'>
                                                <h5 className="card-title">Product name:<br />{data.Product_name}</h5>
                                                <h5 className="card-text">Quantity:{data.quantity}</h5>
                                                <h5 className="card-text">Rs.{data.price}</h5>
                                                <a href='#0' className="btn btn-primary" onClick={()=>{this.Cart(data._id)}}>Add to cart</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Product