import React, { Component } from 'react'
import axios from 'axios'

export  class Login extends Component {

    constructor(props) {
      super(props)
    
      this.state = {
        user: [],
        uname: '',
        pass: '',
        username: '',
        password: ''
      }
    }

    componentDidMount() {
        axios.get("https://enchanting-slippers-bull.cyclic.app/")
          .then((res) => {
            this.setState({ user: res.data.data })
            //console.log(this.state.user);
          })
      }

      inputChnageHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
      }
    
    
    
      user = (e) => {
        this.state.user.map((data) => {
          
            let uname = data.uname
            let pass = data.password
            //console.log(data.uname)//data
            //console.log(data.password)//data
            //console.log(this.state.uname);//input
            //console.log(this.state.password);//input
           //console.log(username);
            //console.log(password);
            if (this.state.uname === uname && this.state.password === pass) {
              alert('login successful')
              window.location = "/cart"
            }
            // else{
            //   alert('error')
            //  filter() method}
            return(uname,pass)
        })
    
        e.preventDefault()
      }
    


    render() {
        return (
            <div>
                <div  className="container-fluid  p-3 bgimg">
                    <div  className="login col-sm-12 col-md-5 col-lg-3  mb-1">
                        <h2  className="active text-light">Login</h2>
                        <hr  className="hr w-25"/>
                            <form>
                                <label  className="text-light mb-2">UserName</label>
                                <input type="text" name="uname"  className="form-control mb-3" placeholder="Enter UserName..." required  onChange={(e) => this.inputChnageHandler(e)}/>

                                <label  className="text-light mb-2">Password</label>
                                <input type="password" name="password"  className="form-control" placeholder="Enter password..." required onChange={(e) => this.inputChnageHandler(e)} />

                                <button  className="btn btn-success mt-3 col-6 signin rounded-pill" type="submit"
                                onClick={(e) => { this.user(e) }}>Login</button>
                                <hr  className="hr"/>

                                    <a href="/signup"  className="textdecor text-light">Don't have an account ? <p  className="text-primary ms-1"> Signup</p></a>
                            </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login