import React, { Component } from 'react'
import { Table } from 'react-bootstrap'
import axios from 'axios'
import { Link } from 'react-router-dom'
export class Dashbord extends Component {

    constructor(props) {
        super(props)

        this.state = {
            Products: [],
            id: null,
            image: '',
            Category: '',
            Product_name: '',
            price: '',
            quantity: ''
        }
    }


    fetchdata = () => {
        axios.get("https://enchanting-slippers-bull.cyclic.app/Products")
            .then((res) => {
                //console.log(res.data)
                this.setState({ Products: res.data.data })
            }).catch((err) => {
                //console.log(err)
            })
    }

    componentDidMount() {
        this.fetchdata()
    }

    deleteRecord = (id) => {
        if (window.confirm('Are you sure you wat to delete this item', +id)) {
            console.log("deleted successfully");
            axios.delete(`https://enchanting-slippers-bull.cyclic.app/Products/${id}`)

                .then(() => {
                    alert("Record Deleted successfully")
                }).catch((err) => {
                    console.log("Error during record deletion")
                })
        }
    }

    render() {
        return (
            <>
                <h3
                    style={{ textAlign: "center" }}>
                    <u>Dashboard Component</u>
                </h3>

                <div className='dash-button'>
                    <div>
                        <h3>Product list:</h3>
                    </div>

                    <div>
                        <button className='btn btn-danger'>
                            <Link to={'/Add'}>Add</Link>
                        </button>
                    </div>
                </div>

                <div className='container-fluid'>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>Image</th>
                                <th>Category</th>
                                <th>Product_name</th>
                                <th>price</th>
                                <th>quantity</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.Products.map((data, index) => {
                                    return <tr key={data._id}>
                                        <td>{data.id}</td>
                                        <td><img src={data.image} alt='images' style={{ width: "200px", height: "200px" }} /></td>
                                        <td>{data.Category}</td>
                                        <td>{data.Product_name}</td>
                                        <td>{data.price}</td>
                                        <td>{data.quantity}</td>
                                        <td>
                                            <button className='btn btn-danger mx-1' onClick={() => { this.deleteRecord(data._id) }}><i className="fa fa-trash" aria-hidden="true"></i></button>
                                            
                                            <button className='btn btn-warning'>
                                                <Link to={'/edit/'+data._id}>
                                                    <i className="fa fa-pencil" aria-hidden="true"></i>
                                                </Link>
                                            </button>
                                        </td>
                                    </tr>
                                })
                            }
                        </tbody>
                    </Table>
                </div>

            </>
        )
    }
}

export default Dashbord