import React, { Component } from 'react'
import img1 from '../Resource/undraw-contact.svg'
export class Contact extends Component {

    constructor(props) {
        super(props)

        this.state = {

        }
    }






    render() {
        return (
            <div className='container-fluid'>
                <div>
                    <div className="row">
                        <div className="col-sm-12 col-md-6 col-lg-6 text-center p-3 font">
                            <h2>Get in touch</h2>
                            <h4>We are always there for our customers!!</h4>
                            <br/>
                                <img src={img1} alt="" className="img1"/>
                                </div>

                                <div className="col-sm-12 col-md-6 col-lg-6 formborder">
                                    <div className="form">
                                        <form className="m-4 borderbox p-3 font">
                                            <label>First name</label>
                                            <input type="text" className="form-control mb-3" placeholder="Enter first_name" required/>

                                            <label>Last name</label>
                                            <input type="text" className="form-control mb-3" placeholder="Enter last_name" required/>

                                            <label>Email</label>
                                            <input type="email" className="form-control mb-3" placeholder="Enter your email" required />

                                            <label>Message</label>
                                            <textarea name="" className="form-control mb-3" rows="7" cols="50" placeholder="Message" />

                                        <button type="submit" className="btn btn-primary">Submit</button>
                                    </form>
                                </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default Contact