import React, { Component } from 'react'
import '../css/Style.css'
import axios from 'axios'
export class Edit extends Component {
  constructor(props) {
    super(props)

    this.state = {
      _id: '',
      image: '',
      Category: '',
      Product_name: '',
      quantity: '',
      price: '',
    }

  }

  inputChnageHandler = (e) => {
    this.setState({ [e.target.name]: e.target.value })
  }

  componentDidMount = () => {
    let url = window.location.pathname;
    let id = url.substring(url.lastIndexOf('/') + 1);
    //console.log(id)
    axios.get(`https://enchanting-slippers-bull.cyclic.app/Products/${id}`)
      .then((res) => {
        console.log(res.data);
        const { _id,image, Category, Product_name, quantity, price } = res.data.data
        this.setState({_id, image, Category, Product_name, quantity, price })
      }).catch((err) => {
        console.log("Error during record deletion")
      })
  }

  editProducts = (e) => {
    console.log("put request", this.state);
    const data = {
      _id:this.state._id,
      image:this.state.image,
      Category:this.state.Category,
      Product_name:this.state.Product_name,
      quantity:this.state.quantity,
      price:this.state.price
    }
    axios.put(`https://enchanting-slippers-bull.cyclic.app/Products/${this.state._id}`,data)
      .then(() => {
        window.alert("data edited successfully!")
        window.location = "/dashbord"
      }).catch((e) => {
        console.log("Error: ", + e);
      })
    // console.log(this.state);
    console.log("data edited");
    e.preventDefault()//to stop default submission
  }

  render() {
    const { image, Category, Product_name, quantity, price } = this.state
    return (
      <>
        <h3 style={{ textAlign: "center" }}>Edit Product Record</h3>
        <div className='addform container-fluid'>
          <form onSubmit={(e) => this.editProducts(e)}>
            <div className="row">
              <div className="col-12">
                <label>Image</label>
                <input type="text" name="image" value={image} className="form-control" placeholder='Enter url' onChange={(e) => this.inputChnageHandler(e)} />
              </div>

              <div className="col-12">
                <label>Category</label>
                <input type="text" name="Category" value={Category} placeholder="Enter product Category" className="form-control" onChange={(e) => this.inputChnageHandler(e)} />
              </div>

              <div className="col-12">
                <label>Product name</label>
                <input type="text" name="Product_name" value={Product_name} placeholder="Enter Product_name" className="form-control" onChange={(e) => this.inputChnageHandler(e)} />
              </div>

              <div className="col-12">
                <label>Quantity</label>
                <input type="text" name="quantity" value={quantity} placeholder="Enter quantity of product" className="form-control" onChange={(e) => this.inputChnageHandler(e)} />
              </div>
            </div>

            <div className="row">

              <div className="col-12">
                <label>Price</label>
                <input type="text" name="price" value={price} placeholder="Enter your Address" className="form-control" onChange={(e) => this.inputChnageHandler(e)} />
              </div>

              <button type="submit" className="done btn btn-primary col-4 col-md-4 m-2">Save</button>
            </div>
          </form>
        </div>

      </>
    )
  }
}

export default Edit