import axios from 'axios'
import React, { Component } from 'react'
import { Table } from 'react-bootstrap'
export class Carts extends Component {

    constructor(props) {
        super(props)

        this.state = {
            cart: [],
            price: '',
            total: 0,
            bill: '',
        }
    }


    componentDidMount() {
        axios.get("https://enchanting-slippers-bull.cyclic.app")
            .then((res) => {
                this.setState({ cart: res.data.data })

                let Bill = this.state.cart.reduce((PreVal, currentVal) => {
                    return PreVal + currentVal.price
                }, 0);
                console.log(Bill);
                this.setState({ bill: Bill })
            })
    }



    deleteRecord = (id) => {
        if (window.confirm('Are you sure you wat to delete this item', +id)) {
            console.log("deleted successfully");
            axios.delete(`https://enchanting-slippers-bull.cyclic.app/cart/${id}`)

                .then(() => {
                    alert("Record Deleted successfully")
                    this.componentDidMount()
                    //this.inputChnageHandler()
                    axios.get("https://enchanting-slippers-bull.cyclic.app/cart")
                        .then((res) => {
                            this.setState({ cart: res.data.data })

                            let Bill = this.state.cart.reduce((PreVal, currentVal) => {
                                return PreVal + currentVal.price
                            }, 0);
                            console.log(Bill);
                            this.setState({ bill: Bill })
                        })
                }).catch((err) => {
                    console.log("Error during record deletion")
                })
        }
    }

    total = (e) => {
        let Bill = this.state.cart.reduce((PreVal, currentVal) => {
            return PreVal + currentVal.price
        }, 0);
        console.log(Bill);
        alert('payment successful')
        window.location = "/"
        e.preventDefault()
    }

    inputChnageHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
        this.componentDidMount()
    }


    render() {
        const { bill } = this.state
        return (
            <div>

                 <button onClick={this.total} className="btn btn-success m-2">Logout</button>

                <div className='container-fluid'>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>Image</th>
                                <th>Category</th>
                                <th>Product_name</th>
                                <th>price</th>
                                <th>quantity</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.cart.map((data, index) => {
                                    return <tr key={data._id}>
                                        <td>{data.id}</td>
                                        <td><img src={data.image} alt='images' style={{ width: "200px", height: "200px" }} /></td>
                                        <td>{data.Category}</td>
                                        <td>{data.Product_name}</td>
                                        <td>{data.price}</td>
                                        <td>{data.quantity}</td>
                                        <td>
                                            <button className='btn btn-danger mx-1' onClick={() => { this.deleteRecord(data._id) }}><i className="fa fa-trash" aria-hidden="true"></i></button>

                                        </td>
                                    </tr>
                                })
                            }
                        </tbody>
                    </Table>
                </div>

                <div className='Total'>
                    <div className="col-sm-3">
                        <form>
                            <div className="row mx-2">
                                <table className="table table-light table-hover">
                                    <thead>
                                        <tr>
                                            <th>Total Bill</th>
                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr>
                                            <td>
                                                <input type="text" name="bill" value={bill} placeholder="Total bill" className="form-control" onChange={(e) => this.inputChnageHandler(e)} />

                                                <button onClick={this.total} className="btn btn-success m-2">Payment</button>
                                            </td>

                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </form>
                    </div>
                </div>

            </div>
        )
    }
}

export default Carts