import React, { Component } from 'react'

export class Signup extends Component {
    render() {
        return (
            <div>
                <div className="container-fluid p-3 bgimg">
                    <div className="login col-sm-12 col-md-6 col-lg-4 col-xl-3 ">
                        <h2 className="active text-light"> Registered Form </h2>
                        <hr className="hr w-50" />
                        <form>
                            <label className="text-light mb-2">First Name</label>
                            <input type="text" name="fname" className="form-control mb-3" placeholder="Enter First Name..." required />

                            <label className="text-light mb-2">Last Name</label>
                            <input type="text" name="lname" className="form-control mb-3" placeholder="Enter Last Name..." required />

                            <label className="text-light mb-2">Username</label>
                            <input type="text" name="uname" className="form-control mb-3" placeholder="Enter Username..." required />

                            <label className="text-light mb-2">Password</label>
                            <input type="password" name="password" className="form-control" placeholder="Enter password..." required />

                            <button className="btn btn-success col-6 signin rounded-pill mt-3">Register</button>
                            <hr className="hr"/>
                                <a href="login" className="textdecor text-light" routerLink="login">
                                Already have an account? <p className="text-primary ms-1"> Login</p>
                                </a>
                        </form>
                    </div>
                </div> 
            </div>
        )
    }
}

export default Signup