import React, { Component } from 'react'
import '../css/Style.css'
import axios from 'axios'
export class Add extends Component {

    constructor(props) {
        super(props)

        this.state = {
            image: '',
            Category: '',
            Product_name: '',
            quantity: '',
            price: '',
        }
    }

inputChnageHandler = (e) => {
this.setState({[e.target.name]:e.target.value})
}

addProduct = (e) =>{
    axios.post("https://enchanting-slippers-bull.cyclic.app/Products",this.state)
    .then(() => {
            window.alert("data added successfully!")
            window.location ="/dashbord"
    }).catch((e)=>{
        console.log("Error: ",+ e);
    })
    // console.log(this.state);
    console.log("data added");
    e.preventDefault()//to stop default submission
}


    render() {
        return (
            <>
                <h3 style={{ textAlign: "center" }}>This is Add Component</h3>
                <div className='addform container-fluid'>
                    <form onSubmit={(e) => this.addProduct(e)}>
                        <div className="row">
                            <div className="col-12">
                                <label>Image</label>
                                <input type="text" name="image" className="form-control" placeholder='Enter url'onChange={(e)=> this.inputChnageHandler(e)}/>
                            </div>

                            <div className="col-12">
                                <label>Category</label>
                                <input type="text" name="Category" placeholder="Enter product Category" className="form-control" onChange={(e)=> this.inputChnageHandler(e)}/>
                            </div>

                            <div className="col-12">
                                <label>Product name</label>
                                <input type="text" name="Product_name" placeholder="Enter Product_name" className="form-control" onChange={(e)=> this.inputChnageHandler(e)}/>
                            </div>

                            <div className="col-12">
                                    <label>Quantity</label>
                                    <input type="text" name="quantity" placeholder="Enter quantity of product " className="form-control" onChange={(e)=> this.inputChnageHandler(e)}/>
                                </div>
                        </div>
    
                            <div className="row">

                                <div className="col-12">
                                    <label>Price</label>
                                    <input type="text" name="price" placeholder="Enter your Address" className="form-control" onChange={(e)=> this.inputChnageHandler(e)} />
                                </div>

                                <button type="submit" className="done btn btn-primary col-4 col-md-4 m-2">Submit</button>
                            </div>
                    </form>
                </div>

            </>
        )
    }
}

export default Add