import React, { Component } from 'react'
import '../css/Style.css'
import axios from 'axios'
export class Admin extends Component {

  constructor(props) {
    super(props)

    this.state = {
      Admin: [],
      uname: '',
      pass: '',
      username: '',
      password: ''
    }
  }


  componentDidMount() {
    axios.get("https://enchanting-slippers-bull.cyclic.app/Admin")
      .then((res) => {
        this.setState({ Admin: res.data.data })
        console.log(this.state.Admin);
      })
  }

  inputChnageHandler = (e) => {
    this.setState({ [e.target.name]: e.target.value })
  }



  admin = (e) => {
    this.state.Admin.map((data) => {
      
        let username = data.uname
        let password = data.pass
        //console.log(this.state.uname);
        //console.log(this.state.pass);
        //console.log(username);
        //console.log(password);
        if (this.state.uname === username && this.state.pass === password) {
          alert('login successful')
          window.location = "/dashbord"
        }
        // else{
        //   alert('error')
        //  filter() method}
        return(username,password)
    })

    e.preventDefault()
  }

 



  render() {
    return (
      <div>
        <div className="container-fluid bgimg1 p-3">

          <div className="login col-sm-12 col-md-5 col-lg-3 col-xl-3  mb-3">
            <h2 className="active text-light">Welcome Admin</h2>
            <hr className="hr w-25" />
            <form>
              <label className="text-light mb-2">UserName</label>
              <input type="text" name="uname" className="form-control mb-3" placeholder="Enter UserName..." required onChange={(e) => this.inputChnageHandler(e)} />

              <label className="text-light mb-2">Password</label>
              <input type="password" name="pass" className="form-control" placeholder="Enter password..." required onChange={(e) => this.inputChnageHandler(e)} />

              <button className="btn btn-success mt-3 col-6 signin rounded-pill" type="submit" onClick={(e) => { this.admin(e) }}>login</button>
              <hr className='hr' />
            </form>
          </div>
        </div>
        <div>

        </div>
      </div>

    )
  }
}

export default Admin