import React, { Component } from 'react'
import logo from '../Resource/angular_project_logo2-removebg-preview.png'
import '../css/Style.css'
export class Footer extends Component {
    render() {
        return (
            <>
                <div className="container-fluid ">

                    <div className="row bgfooter text text-light fontStyle footer fixfooter">
                        <div className="col-sm-12 col-md-3">
                            <div className="logo">
                                <a className="" href="/">
                                    <img src={logo} alt="Bootstrap" width="200px" height="140px" />
                                </a>
                            </div>

                            <div className="description">
                            <p>Step into the world of sweetness and delight  where every bite is a celebration of traditional Indian cuisine.</p>
                                <p></p>
                            </div>

                        </div>

                        <div className='col-sm-12 col-md-2 mt-3'>
                            <h5>Quick Links</h5>
                            <div className='list'>
                                <ul>
                                    <li><a href="/">Home</a></li>
                                    <li><a href="/product">Products</a></li>
                                    <li><a href="/contact">Contact</a></li>
                                    <li><a href="/cart">Cart</a></li>
                                </ul>
                            </div>
                        </div>

                        <div className='col-sm-12 col-md-3 mt-3'>
                            <h5>Contact Information</h5>
                            <p className='Info'>
                                <i className="fa fa-map-marker" aria-hidden="true" />      Address:<br /> Meethi   Manuhar 123, AC Road <br /> Shivaji Nagar
                                Pune, Maharashtra 411005 <br />

                                <i className="fa fa-phone" aria-hidden="true"></i>     Contact: +91 7855962144<br />

                                <i className="fa fa-envelope-o" aria-hidden="true"></i>  Mail:MeethiManuhar@dmail.com
                            </p>

                        </div>

                        <div className='col-sm-12 col-md-2 mt-3'>
                            <h5 className="text-center mb-2">Follow us</h5>
                            <div className="social text text-center">
                                <i className="social fa fa-facebook" aria-hidden="true"></i>
                                <i className="social fa fa-instagram" aria-hidden="true"></i>
                                <i className="social fa fa-twitter" aria-hidden="true"></i>
                            </div>
                        </div>

                        <hr />
                        <div className=" text text-light text-center bgfooter">
                            All Rights Reserved © Meethi Manuhar 2018 | Privacy Policy | Terms of Service
                        </div>

                    </div>

                </div>
            </>//main
        )
    }
}

export default Footer