import React, { Component } from 'react'
import img1 from '../Resource/Carousel images/m1.jpg'
import img2 from '../Resource/Carousel images/m66.jpg'
import img3 from '../Resource/Carousel images/m22.jpg'
import img4 from '../Resource/Carousel images/m88.jpg'

import car1 from '../Resource/Carousel images/o1.png'
import car2 from '../Resource/sweetshop images/angular project logo2.png'
import car3 from '../Resource/Carousel images/ms1.jpg'
import chef from '../Resource/chef.jpg'
import '../css/Style.css'
import axios from 'axios'
export class Home extends Component {

  constructor(props) {
    super(props)

    this.state = {
      Products: [],
      id: null,
      image: '',
      Category: '',
      Product_name: '',
      price: '',
      quantity: ''
    }
  }

  fetchdata = () => {
    axios.get("https://enchanting-slippers-bull.cyclic.app/Bestseller")
      .then((res) => {
        console.log(res.data)
        this.setState({ Products: res.data.data })
      }).catch((err) => {
        //console.log(err)
      })
  }

  componentDidMount() {
    this.fetchdata()
  }



  render() {

    return (
      <div className="container-fluid">
        <div id="carouselExampleInterval" className="carousel slide hide" data-bs-ride="carousel">
          <div className="carousel-inner">
            <div className='carousel-item active' data-bs-interval="100">
              <img src={img1} className="d-block w-100" style={{ height: 500 }} alt="..." />
            </div>
            <div className="carousel-item" data-bs-interval="1000">
              <img src={img2} className="d-block w-100" style={{ height: 500 }} alt="..." />
            </div>
            <div className="carousel-item" data-bs-interval="1000">
              <img src={img3} className="d-block w-100" style={{ height: 500 }} alt="..." />
            </div>
            <div className="carousel-item" data-bs-interval="1000">
              <img src={img4} className="d-block w-100" style={{ height: 500 }} alt="..." />
            </div>
          </div>
          <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Previous</span>
          </button>
          <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Next</span>
          </button>
        </div>
        <hr />

        <div className='container-fluid d-flex justify-content-evenly flex-wrap'>
          {
            this.state.Products.map((data, index) => {
              return <div key={data.id}>
                <div className='row  p-3'>
                  <div className='col text-center'>
                    <div className='card border border-dark rounded mt-2 col-12 col-md-6 col-lg-3' style={{ height: 290, width: 250 }}>
                      <img src={data.image} className="d-block w-100" style={{ height: 100 }} alt="..." />

                      <div className='card-body'>
                        <h5 className="card-title">Product name:<br/>{data.Product_name}</h5>
                        <h5 className="card-text">Quantity:{data.quantity}</h5>
                        <h5 className="card-text">Rs.{data.price}</h5>
                        <a href='/products' className="btn btn-primary">View more</a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            })
          }
        </div>



        <hr />

        <div className="container-fluid">

          <div className="row">

            <div className="col-12 col-md-6 welcome">
              <img src={car2} alt="..."/>
            </div>

            <div className="col-12 col-md-6 p-3">
              <h2 className="c1">Our Story</h2>
              <h4 className="">Welcome to <span className="title">Meethi Manuhaar</span></h4>
              <p className="about">Meethi Manuhaar is a traditional Indian sweet shop located in the heart of the city. Established in the year 1990, Meethi Manuhaar has been serving its customers with delicious and authentic Indian sweets for over 30 years. With its wide range of sweets including traditional sweets like Gulab Jamun, Rasgulla, Kulfi and Kaju Katli, Meethi Manuhaar is a one-stop-shop for all your sweet cravings..</p>
              <p className="about">
                The shop is known for its use of high-quality ingredients and traditional recipes, which are passed down from generations, making each bite of the sweets as authentic as it can be. Whether you're celebrating a special occasion or just in the mood for some sweetness, Meethi Manuhaar is a must-visit for anyone who loves Indian sweets..
              </p>
              <blockquote className="goldline">
                <p className="ms-3 about">Meethi Manuhaar is an exceptional sweet shop that offers authentic Indian sweets. The flavors are rich and the quality is exceptional. A must-visit for all sweet lovers..</p>
                <cite className="ms-2">- James Doe</cite>
              </blockquote>
            </div>

          </div>

        </div>

        <div className='container-fluid'>

          <div className='row'>

            <div className="col-sm-12 col-md-6">
              <h2 className="bs c1 mt-2">The History</h2>
              <h4 className="">Best food in town since  <span className="title">1990</span></h4>
              <p className="about">Meethi Manuhaar Indian Sweet Shop was founded in 1990 by a young man named Manu who had a passion for making sweets. Manu learned the art of making sweets from his grandmother who used to run a small sweet shop in their village. Manu started Meethi Manuhaar with a small cart selling traditional Indian sweets such as Gulab Jamun, Laddu, Jalebi</p>
              <p className="about">
                With time, Manu's sweets became popular and he opened a small shop in the busy market of Delhi. Today, Meethi Manuhaar is a well-known sweet shop with several branches all over India and is known for its authentic and delicious sweets. People from all over the world come to taste Manu's sweets and experience the rich history and culture of Indian sweets.
              </p>
              <p className="about">"Step into the world of sweetness and delight with Meethi Manuhaar Indian Sweet Shop, where every bite is a celebration of traditional Indian cuisine. Let us treat your taste buds with our delicious and authentic sweets, handcrafted with love and passion." </p>
            </div>
            <div className="col-sm-12 col-md-6">
              <div id="carouselExampleSlidesOnly" className="carousel slide" data-bs-ride="carousel">
                <div className="carousel-inner">
                  <div className="carousel-item  active" data-bs-interval="1000">
                    <img src={car1} className="d-block w-100" alt="..." style={{ height: 400 }} />
                  </div>
                  <div className="carousel-item" data-bs-interval="1000">
                    <img src={chef} className="d-block w-100" alt="..." style={{ height: 400 }} />
                  </div>
                  <div className="carousel-item" data-bs-interval="1000">
                    <img src={car3} className="d-block w-100" alt="..." style={{ height: 400 }} />
                  </div>
                </div>
              </div>
            </div>

          </div>

        </div>

        <div className='container-fluid'>
          <div className='row opening m-2'>
            <div className='col openingbackground'>
              <div className='openinghours'>
                <h2 className="c1"><u>Opening Hours</u></h2>
              </div>
              <div className='row'>
                <div className="col-sm-12 col-md-6 part1 text-center">
                  <h3 className="c1">Monday to Friday</h3>
                  <p className="text-light time">9:00 AM</p>
                  <hr className='titleline' />
                  <p className="text-light time">11:00 PM</p>
                </div>
                <div className="col-sm-12 col-md-6 text-center">
                  <h3 className="c1">Saturday to Sunday</h3>
                  <p className="text-light time">10:00 AM</p>
                  <hr className='titleline' />
                  <p className="text-light time">2:00 PM</p>
                </div>
              </div>
            </div>
          </div>
        </div>






































      </div>//end
    )
  }
}

export default Home